@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('Montserrat-Regular.woff') format('woff'),
    url('Montserrat-Regular.woff2') format('woff2');
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url('Montserrat-Bold.woff') format('woff'),
    url('Montserrat-Bold.woff2') format('woff2');
}
